.Button {
  flex-grow: 1;
  color: rgb(88, 73, 73);
  cursor: pointer;
  width: 2rem;
  height: 2rem;
  margin: 0.2rem;
  padding: 0;
  text-align: center;
  font-size: 0.9rem;
  background-color: white;
  border: 2px solid whitesmoke;
  border-radius: 4px;
  box-shadow: 0px 1px 1px whitesmoke,
              0px 2px 0px gray,
              0px -1px 1px 1px lightgray;
}

.Button[name="calculate"] {
  font-weight: 900;
  color: violet;
}

@media (min-width: 500px) {
  .Button {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.1rem;
  }
}